import React, { Component } from 'react';
import fb from './firebase';
import data from './data/data.json';
import Header from './header';
import MentorSelect from './select';
import DrawTable from './tableDraw';


class App extends Component {
  state = localStorage.mentor ? { mentor: JSON.parse(localStorage.getItem('mentor')), boolean: true } : { mentor: { mentor: '', github: '', students: [] }, boolean: true };

  handleMentor = (value) => {
    const mentorValue = data.mentors.find(mentor => mentor.mentor === value.label);
    this.setState({ mentor: mentorValue });
    localStorage.setItem('mentor', JSON.stringify(mentorValue));
  };

  login = () => {
    fb.login().then(({ user }) => this.setState({ user, boolean: false }));
  }

  logout = () => {
    fb.logout().then(() => this.setState({ user: null, boolean: true }));
  }

  render() {
    const { mentor: { mentor, github, students }, user, boolean } = this.state;
    const { tasks, mentors } = data;

    const listOfMentors = mentors.map(unit => ({ value: `${unit.mentor} ${unit.github}`, label: unit.mentor }));
    const currentMentor = listOfMentors.find(el => el.label === mentor);

    return (
      <section>
        <Header
          user={user}
          login={this.login}
          logout={this.logout}
        />
        <MentorSelect
          list={listOfMentors}
          setMentor={this.handleMentor}
          state={currentMentor}
          boolean={boolean}
        />
        <DrawTable
          mentor={github}
          students={students}
          tasksList={tasks}
          boolean={boolean}
        />
      </section>
    );
  }
}

export default App;
