import React from 'react';
import Select from 'react-select';
import PropTypes from 'prop-types';

const customStyles = {
  container: provided => ({
    ...provided,
    width: '50%',
    margin: 'auto',
    'margin-top': 20,
    'margin-bottom': 20,
  }),
  option: provided => ({
    ...provided,
    color: 'green',
    padding: 10,
  }),
  singleValue: (provided) => {
    const color = 'green';

    return { ...provided, color };
  },
};

const MentorSelect = ({
  list, setMentor, state, boolean,
}) => (
  <Select
    placeholder="Choose mentor..."
    styles={customStyles}
    options={list}
    onChange={setMentor}
    defaultValue={state}
    isDisabled={boolean}
  />
);

MentorSelect.propTypes = {
  list: PropTypes.arrayOf(PropTypes.object).isRequired,
  setMentor: PropTypes.func.isRequired,
  state: PropTypes.shape(
    {
      value: PropTypes.string,
      label: PropTypes.string,
    },
  ).isRequired,
  boolean: PropTypes.bool.isRequired,
};

export default MentorSelect;
