import React from 'react';
import PropTypes from 'prop-types';

const DrawTable = ({ students, tasksList, mentor }) => {
  const head = Object.entries(tasksList).map(([task, value]) => <th key={task}><a href={value.link} rel="noopener noreferrer" target="_blank">{task}</a></th>);

  const body = students.map((student) => {
    const { tasks } = student;

    const result = tasks.map((task) => {
      const className = task.status === 'Checked' && task.score === 0 ? 'NotDone' : task.status.replace(' ', '-');
      return <td key={task.name.concat('-key')} className={className}>{task.score}</td>;
    });

    return (
      <tr key={student.student}>
        <td className="student-github"><a href={student.github} rel="noopener noreferrer" target="_blank">{student.student}</a></td>
        {result}
      </tr>
    );
  });

  return students.length
    ? (
      <div className="table-container">
        <table>
          <thead>
            <tr>
              <th colSpan="11" className="mentor-github">
                {'Mentor: '}
                <span>{mentor}</span>
              </th>
            </tr>
            <tr>
              <th rowSpan="2">Students</th>
              <th colSpan="10">Tasks</th>
            </tr>
            <tr>
              {head}
            </tr>
          </thead>
          <tbody>
            {body}
          </tbody>
        </table>
        <div className="table-legend">
          <span className="table-legend-unit">Task is checked</span>
          <span className="table-legend-unit">Task is in progress</span>
          <span className="table-legend-unit">Task has to be checked</span>
          <span className="table-legend-unit">Task is planned</span>
          <span className="table-legend-unit">Task is not done</span>
        </div>
      </div>
    )
    : <p className="message_tip">Choose mentor to see a table...</p>;
};

DrawTable.propTypes = {
  students: PropTypes.arrayOf(PropTypes.object).isRequired,
  tasksList: PropTypes.shape(PropTypes.shape).isRequired,
  mentor: PropTypes.string.isRequired,
};

export default DrawTable;
